import React from 'react';
import SearchBox from './SearchBox';
import { Link } from "react-router-dom";

export default function ComongSoon() {
    return (
        <>
            <section className="page-top-section set-bg" style={{ backgroundImage: "url('/img/page-top-bg.jpg')"}}>
                <div className="page-top-warp">
                    <SearchBox />
                </div>
            </section>
            <section id="main-container" className="main-container">
                <div className="container">

                    <div className="row">

                        <div className="col-12">
                            <div className="error-page text-center">
                                <h2 className="soon_head">Coming Soon</h2>
                                <h5 className="not_fnd_hd">This Page is under construction</h5>
                                <p className="not_fnd_body">
                                    Try using the button below to go to main page of the site <br />
                                    <Link to="/" className="readmore-btn">Back to Home Page</Link>
                                </p>
                            </div>
                        </div>

                    </div>
                </div>
            </section>
        </>
    )
}
